import ListDetalhes from './listDetalhes';

const DetalhesGroups = (props) => {
    const detalhesCurrent = props.detalhes;
    return(
        <div>
            {detalhesCurrent.map((singleGroup) => {
                return (
                <div key={singleGroup.slug} className='my-2 font-medium text-lg'>
                    <h3>{singleGroup.nome}</h3>
                    <ListDetalhes content={singleGroup.content} />
                </div>
                )
            })}
        </div>
    )
}
export default DetalhesGroups;