import Modal from './modal';

const ListDetalhes = (props) => {
    return (
        <div className="flex font-normal flex-wrap">
            {props.content.length ? props.content.map((singleContent) => {
                return(
                    <Modal dados={singleContent} />
                )
            }) : <div class="bg-yellow-100 text-yellow-800 my-2 py-1 px-2 rounded-md text-sm"><i class="fa-solid fa-spinner text-base  fa-fw fa-2x fa-spin"></i> Atualizaremos em breve</div> }
        </div>
    )
}
export default ListDetalhes;