
import DetalhesGroups from './detalhesGroups';


const Card = (props) => {
    
    const detalhes = props.content.detalhes;

    return (
        <div className="bg-gray-100 rounded-xl px-2 md:px-4 py-6 m-1 md:m-4" key={props.content.slug}>
            <img
                src={props.content.img}
                className="h-28 md:h-36 w-28 md:w-36 mx-auto object-cover rounded-full border-4 border-gray-200"
                alt=""
            />
            <div className="my-2">
                <h2 className="text-base md:text-xl font-medium uppercase font-brandon">
                    {props.content.nome}
                </h2>
                <h3 className='text-xs'>{props.content.nomeCompleto}</h3>
            </div>
            <div class="flex flex-col items-center text-center md:text-left md:block">
                <div class="font-bold py-2 text-base md:text-xl flex justify-center flex-wrap text-gray-900 gap-3 md:gap-5">
                    <div>
                        <a href={props.content.redes.site} class=" text-blue-500 hover:text-gold-500 transition" target="_blank" rel="noreferrer">
                            <i class="fa-solid fa-globe"></i>
                        </a>
                    </div>
                    <div>
                        <a href={props.content.redes.facebook} class=" text-blue-500 hover:text-gold-500 transition" target="_blank" rel="noreferrer">
                            <i class="fa-brands fa-square-facebook"></i>
                        </a>
                    </div>
                    <div>
                        <a href={props.content.redes.instagram} class=" text-blue-500 hover:text-gold-500 transition" target="_blank" rel="noreferrer">
                            <i class="fa-brands fa-instagram"></i>
                        </a>
                    </div>
                    <div>
                        <a href={props.content.redes.twitter} class=" text-blue-500 hover:text-gold-500 transition" target="_blank" rel="noreferrer">
                            <i class="fa-brands fa-twitter"></i>
                        </a>
                    </div>
                    <div>
                        <a href={props.content.redes.youtube} class=" text-blue-500 hover:text-gold-500 transition" target="_blank" rel="noreferrer">
                            <i class="fa-brands fa-youtube"></i>
                        </a>
                    </div>
                    <div>
                        <a href={props.content.redes.linkedin} class=" text-blue-500 hover:text-gold-500 transition" target="_blank" rel="noreferrer">
                            <i class="fa-brands fa-linkedin"></i>
                        </a>
                    </div>
                    <div>
                        <a href={props.content.redes.tiktok} class=" text-blue-500 hover:text-gold-500 transition" target="_blank" rel="noreferrer">
                            <i class="fa-brands fa-tiktok"></i>
                        </a>
                    </div>
                </div>
            </div>
            <div className="mx-auto">
                <a href={props.content.propostas} target="_blank" rel="noreferrer">
                    <button class="text-blue-500 border border-blue-500 hover:text-white hover:bg-blue-500 text-xs md:text-sm transition rounded-md px-2 py-1">Ver Propostas</button>
                </a>
            </div>
            <div className="my-2 text-left">
                <p className="text-xs text-gray-500 pt-1"><strong>Nascimento: </strong> {props.content.nascimento}</p>
                <p className="text-xs text-gray-500 pt-1"><strong>Presidente: </strong> {props.content.ordem}</p>
                <p className="text-xs text-gray-500 pt-1"><strong>Período: </strong> {props.content.periodo}</p>
            </div>
            <div className="flex item-center justify-center text-sm bg-gray-200 rounded-md p-1">
                <img
                    src={props.content.partido.img}
                    className="h-6 mr-1"
                    alt=""
                />
                <p className="text-gray-500">{props.content.partido.nome}</p>
            </div>
            <hr className="border-gray-300 my-2" />
            <div className="flex flex-col md:flex-row justify-start items-center">
                <div>
                    <img
                        src={props.content.vice.img}
                        className="h-20 w-20 mx-auto object-cover rounded-full border-4 border-gray-200"
                        alt=""
                    />
                </div>
                <div className="flex flex-col items-center md:items-start px-3">
                    <h4 className="uppercase font-medium text-xs">Vice</h4>
                    <h3 className="font-medium">{props.content.vice.nome}</h3>
                    <p className="text-xs">{props.content.vice.partido}</p>
                </div>
            </div>
            <hr className="border-gray-300 my-2" />
            
            <div className='text-left'>
                <div>
                    <h1 className='uppercase font-medium text-sm'>Recursos de Campanha</h1>
                    <div className="mx-auto my-1">
                        <a href={props.content.financeiro.link} target="_blank" rel="noreferrer">
                            <button class="text-blue-500 border border-blue-500 hover:text-white hover:bg-blue-500 text-xs md:text-sm transition rounded-md px-2 py-1">Ver Divulgação de Contas</button>
                        </a>
                    </div>
                    <div className="text-left">
                        <p className="text-base text-red-900 pt-2"><div className='text-gray-400 font-bold text-xs font-brandon uppercase'>Total líquido de Recursos Recebidos: </div> {props.content.financeiro.recebidos}</p>
                        <p className="text-base text-gray-900 pt-2"><div className='text-gray-400 font-bold text-xs font-brandon uppercase'>Recursos Financeiros </div> {props.content.financeiro.recursos}</p>
                        <p className="text-base text-gray-900 pt-2"><div className='text-gray-400 font-bold text-xs font-brandon uppercase'>Doação de Pessoas Físicas </div> {props.content.financeiro.doacao}</p>
                        <p className="text-base text-gray-900 pt-2"><div className='text-gray-400 font-bold text-xs font-brandon uppercase'>Limite de Gastos </div> {props.content.financeiro.limite}</p>
                        <p className="text-base text-red-900 pt-2"><div className='text-gray-400 font-bold text-xs font-brandon uppercase'>Total de Despesas Contratadas </div> {props.content.financeiro.pagos}</p>
                    </div>
                </div>
            </div>
            <hr className="border-gray-300 my-2" />
            <div className="text-left">
                <DetalhesGroups detalhes={detalhes} />
            </div>
        </div>
    );
}

export default Card;