
import { Dialog, Transition } from '@headlessui/react'
import { Fragment, useState } from 'react'

function Modal (props) {
    
  let [isOpen, setIsOpen] = useState(false)

  function closeModal() {
    setIsOpen(false)
  }

  function openModal() {
    setIsOpen(true)
  }
  return (
    <>
        <div key={props.dados.slug} onClick={openModal} className="cursor-pointer mx-1 my-1 px-2 py-1 rounded-sm md:rounded-md text-xs md:text-sm bg-gray-300 hover:bg-blue-500 hover:text-white transition">
            <p>{props.dados.nome}</p>
        </div>
        <Transition appear show={isOpen} as={Fragment}>
            <Dialog as="div" className="relative z-10 style-modal" onClose={closeModal}>
            <Transition.Child
                as={Fragment}
                enter="ease-out duration-300"
                enterFrom="opacity-0"
                enterTo="opacity-100"
                leave="ease-in duration-200"
                leaveFrom="opacity-100"
                leaveTo="opacity-0"
            >
                <div className="fixed inset-0 bg-black bg-opacity-25" />
            </Transition.Child>

            <div className="fixed inset-0 overflow-y-auto">
                <div className="flex min-h-full items-center justify-center p-4 text-center">
                <Transition.Child
                    as={Fragment}
                    enter="ease-out duration-300"
                    enterFrom="opacity-0 scale-95"
                    enterTo="opacity-100 scale-100"
                    leave="ease-in duration-200"
                    leaveFrom="opacity-100 scale-100"
                    leaveTo="opacity-0 scale-95"
                >
                    <Dialog.Panel className="w-full max-w-2xl transform overflow-hidden rounded-2xl bg-white p-6 text-left align-middle shadow-xl transition-all">
                    <Dialog.Title
                        as="h3"
                        className="text-lg font-medium leading-6 text-gray-900"
                    >
                        {props.dados.nome}
                    </Dialog.Title>
                    <div className="mt-2">
                        <p className="text-sm text-gray-500">
                        <div dangerouslySetInnerHTML={{ __html: props.dados.texto }} />
                        </p>
                        {props.dados.referencia ? (<p class="text-gray-500 text-xs">Fonte da Matéria: <br /> {props.dados.referencia}</p>) : (null)}
                    </div>

                    <div className="mt-4">
                        <button
                        type="button"
                        className="inline-flex justify-center rounded-md border border-transparent bg-blue-100 m-2 px-4 py-2 text-sm font-medium text-blue-900 hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2"
                        onClick={closeModal}
                        >Fechar</button>
                        {
                            props.dados.referencia ? (
                                <a href={props.dados.referencia} target="_blank" rel="noreferrer">
                                <button type="button" className="inline-flex justify-center rounded-md border border-transparent bg-green-100 m-2 px-4 py-2 text-sm font-medium text-green-900 hover:bg-green-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2">Ver Fonte</button>
                                </a>
                            ) : (
                                null
                            )
                        }
                    </div>
                    </Dialog.Panel>
                </Transition.Child>
                </div>
            </div>
            </Dialog>
        </Transition>
    </>
  );
}
export default Modal;