                    'texto': '<iframe width="560" height="315" src="https://www.youtube.com/embed/LD8-b4wvIjc" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> <br /> <iframe width="560" height="315" src="https://www.youtube.com/embed/j7B1GpGR1Yw" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> <br /> <iframe width="560" height="315" src="https://www.youtube.com/embed/B68cbpzgp58" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> <br /> <iframe width="560" height="315" src="https://www.youtube.com/embed/KG9BRqB94Eo" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>',
                    'referencia': 'https://www.youtube.com/watch?v=LD8-b4wvIjc',
